import React from 'react'
import { Category } from '../../Category'
import { Error } from '../../../ui/Error'
import { Loading } from '../../../ui/Loading'
import { ViewDetails } from '../../ViewDetails'

export const BeefRecipes = props => {
  const { baseUri, beefRecipes, error, loading, openModal, state } = props

  if (error)
    return <Error err={error} />

  if (loading)
    return <Loading />

  if (beefRecipes) {
    return (
      <div>
        <Category baseUri={baseUri} category={beefRecipes} openModal={openModal} title='Beef' />
        <ViewDetails close={openModal} id={state.id} show={state.show} />
      </div>
    )
  } else {
    return null
  }
}
