import React from 'react'
import { Category } from '../../Category'
import { Error } from '../../../ui/Error'
import { Loading } from '../../../ui/Loading'
import { ViewDetails } from '../../ViewDetails'

export const TurkeyRecipes = props => {
  const { baseUri, error, loading, openModal, state, turkeyRecipes } = props

  if (error)
    return <Error err={error} />

  if (loading)
    return <Loading />

  if (turkeyRecipes) {
    return (
      <div>
        <Category baseUri={baseUri} category={turkeyRecipes} openModal={openModal} title='Turkey' />
        <ViewDetails close={openModal} id={state.id} show={state.show} />
      </div>
    )  
  } else {
    return null
  }
}
