import React from 'react'
import { Category } from '../../Category'
import { Error } from '../../../ui/Error'
import { Loading } from '../../../ui/Loading'
import { ViewDetails } from '../../ViewDetails'

export const MaxCaloriesRecipes = props => {
  const { error, loading, maxCaloriesRecipes, openModal, state } = props

  if (error)
    return <Error err={error} />

  if (loading)
    return <Loading />

  if (maxCaloriesRecipes) {
    return (
      <div>
        <Category baseUri='' category={maxCaloriesRecipes} openModal={openModal} title='Under 500 calories' />
        <ViewDetails close={openModal} id={state.id} show={state.show} />
      </div>
    )
  } else { 
    return null
  }
}
