import React from 'react'
import { Category } from '../../Category'
import { Error } from '../../../ui/Error'
import { Loading } from '../../../ui/Loading'
import { ViewDetails } from '../../ViewDetails'

export const TenMinutesRecipes = props => {
  const { error, loading, openModal, state, tenMinutesRecipes } = props

  if (error)
    return <Error err={error} />

  if (loading)
    return <Loading />

  if (tenMinutesRecipes) {
    return (
      <div>
        <Category baseUri='' category={tenMinutesRecipes} openModal={openModal} title='10 Minutes' />
        <ViewDetails close={openModal} id={state.id} show={state.show} />
      </div>
    )
  } else {
    return null
  }    
}
