import React, { PureComponent } from 'react'
import { Redirect } from 'react-router-dom'
import { BeefRecipes } from '../Recipes/BeefRecipes'
import { ChickenRecipes } from '../Recipes/ChickenRecipes'
import { MaxCaloriesRecipes } from '../Recipes/MaxCaloriesRecipes'
import { PorkRecipes } from '../Recipes/PorkRecipes'
import { TenMinutesRecipes } from '../Recipes/TenMinutesRecipes'
import { TurkeyRecipes } from '../Recipes/TurkeyRecipes'
import { VegetarianRecipes } from '../Recipes/VegetarianRecipes'

export class Cookbook extends PureComponent {
  state = {
    id: 0,
    show: false
  }

  openModal = id => {
    this.setState({
      id: id,
      show: !this.state.show
    })
  }
  
  render() {
    const { auth } = this.props
  
    if (!auth.uid)
      return <Redirect to='/sign-in' /> 
  
    return (
      <div>
        <BeefRecipes state={this.state} openModal={this.openModal} />
        <ChickenRecipes state={this.state} openModal={this.openModal} />
        <PorkRecipes state={this.state} openModal={this.openModal} />
        <TurkeyRecipes state={this.state} openModal={this.openModal} />
        <VegetarianRecipes state={this.state} openModal={this.openModal} />
        <TenMinutesRecipes state={this.state} openModal={this.openModal} />
        <MaxCaloriesRecipes state={this.state} openModal={this.openModal} />
      </div>
    )
  } 
}
