import React from 'react'
import { Category } from '../../Category'
import { Error } from '../../../ui/Error'
import { Loading } from '../../../ui/Loading'
import { ViewDetails } from '../../ViewDetails'

export const PorkRecipes = props => {
  const { baseUri, error, loading, openModal, porkRecipes, state } = props

  if (error)
    return <Error err={error} />

  if (loading)
    return <Loading />

  if (porkRecipes) {
    return (
      <div>
        <Category baseUri={baseUri} category={porkRecipes} openModal={openModal} title='Pork' />
        <ViewDetails close={openModal} id={state.id} show={state.show} />
      </div>
    )
  } else {
    return null
  }
}
